import { useEffect, useState } from "react";
import {
  Box,
  Collapse,
  Divider,
  FormControl,
  Link,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  MenuItem,
  OutlinedInput,
  Select,
  SelectChangeEvent,
  Typography,
} from "@mui/material";
import { withStyles } from "@mui/styles";
import { ExpandLess, ExpandMore } from "@mui/icons-material";

import { useBusinessData, useBusinessApi } from "Contexts/Business";
import { AppLink } from "Components/Common";
import { BrandLogo, Gear, Peoples, Lead, WhatsApp, Social } from "Assets/Icons";

const StyledMenuItem = withStyles({
  root: {
    alignItems: "center",
    columnGap: "10px",
    display: "flex",
    justifyContent: "flex-start",
    padding: "6px 16px",

    "& .image, & .link": {
      display: "flex",
    },
  },

  notchedOutline: {
    border: "none",
  },
})(MenuItem);

const StyledListItem = withStyles({
  root: {
    borderRadius: "12px",
    cursor: "pointer",
    marginBottom: "6px",
    width: "100%",
    height: "42px",

    "&:hover": {
      backgroundColor: "rgba(159, 159, 159, 0.1)!important",

      "& svg": {
        "& path": {
          fill: "#464A51",
        },
      },

      "& .MuiListItemText-primary": {
        color: "#464A51",
      },
    },

    "& .MuiListItem-root": {
      paddingLeft: "32px",
    },

    "&.Mui-selected": {
      backgroundColor: "#32c5ff1a!important",

      "& svg": {
        "& path": {
          fill: "#464A51",
        },
      },

      "& .MuiListItemText-primary": {
        color: "#464A51",
      },
    },

    "& .MuiListItemIcon-root": {
      minWidth: "30px",
    },

    "& svg": {
      "& path": {
        fill: "#686C72",
      },
    },

    "& .MuiListItemText-primary": {
      color: "#686C72",
      fontSize: "15px!important",
      fontWeight: 500,
      lineHeight: "18px!important",
    },
  },
})(ListItem);

const Sidebar = () => {
  const { allBusiness, selectedBusiness } = useBusinessData();
  const { getAllUserBusiness, fetchDefaultBusiness } = useBusinessApi();
  const [open, setOpen] = useState<string | false>(false);
  const handleClick = (panel: string) => {
    setOpen((opened) => (opened === false || opened !== panel ? panel : false));
  };
  const exitSetting = () => {
    const redirectTo = selectedBusiness?.default_url
      ? selectedBusiness.default_url
      : "/engage/chat";
    window.location.href = redirectTo;
  };
  const switchBetweenBusiness = (event: SelectChangeEvent) => {
    console.log(event);
    // Todo: Add Switch Business logic here
  };

  useEffect(() => {
    allBusiness.length === 0 && getAllUserBusiness();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allBusiness]);

  useEffect(() => {
    !selectedBusiness && fetchDefaultBusiness();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedBusiness]);

  return (
    <Box
      height="100%"
      display="flex"
      padding="0 24px"
      alignItems="stretch"
      flexDirection="column"
      justifyContent="space-between"
    >
      <Box
        id="head"
        display=" flex"
        padding="30px 0"
        rowGap={2.5}
        flexDirection="column"
      >
        <Box id="above" display="flex" columnGap="12px" alignItems="center">
          <BrandLogo />
          <Typography
            component="h4"
            id="page-title"
            color="#24272C"
            fontSize={18}
            fontWeight={700}
            lineHeight="21px"
            letterSpacing={0}
          >
            {"BUSINESS SETTINGS"}
          </Typography>
        </Box>
        <Box
          className="below"
          sx={{ display: "flex", flexDirection: "column" }}
        >
          <Typography
            component="small"
            color="#24272C80"
            fontSize={10}
            fontWeight={700}
            lineHeight="12px"
            letterSpacing={0}
            mb={2 / 3}
          >
            {"SELECT BUSINESS"}
          </Typography>
          <FormControl
            variant="filled"
            fullWidth
            sx={{
              boxShadow: "0px 2px 6px rgba(0, 0, 0, 0.15)",
              borderRadius: 2,
              backgroundColor: "#ffffff",
            }}
          >
            {selectedBusiness && allBusiness.length > 0 && (
              <Select
                input={<OutlinedInput />}
                inputProps={{ "aria-label": "Without label" }}
                name="business"
                value={`${selectedBusiness?.id}` || ""}
                onChange={switchBetweenBusiness}
                sx={{
                  p: 0,

                  "& .MuiSelect-select.MuiOutlinedInput-input": {
                    alignItems: "center",
                    columnGap: 1.25,
                    display: "flex",
                    height: 32,
                    py: 1,
                    pl: 2.5,
                    justifyContent: "flex-start",
                  },

                  "& .MuiOutlinedInput-notchedOutline": {
                    border: "none",
                  },
                }}
              >
                {allBusiness.map(({ business }, i) => (
                  <StyledMenuItem key={i} value={business.id}>
                    <Box
                      component="span"
                      id="image"
                      sx={{
                        backgroundColor: "#24272C1A",
                        height: "20px",
                        width: "20px",
                      }}
                    >
                      {business.logo && business.logo !== "null" && (
                        <img
                          src={business.logo}
                          alt={`Business: ${business.business_name}`}
                          style={{ width: "20px" }}
                        />
                      )}
                    </Box>
                    <Box component="span" id="label">
                      {business.business_name}
                    </Box>
                  </StyledMenuItem>
                ))}
              </Select>
            )}
          </FormControl>
        </Box>
      </Box>
      <List id="menu" sx={{ flex: "1 0 0", overflow: "auto" }}>
        <AppLink to="/business/general-settings" aria-label="General Settings">
          <StyledListItem
            onClick={() => setOpen(false)}
            selected={window.location.pathname.includes("general-settings")}
          >
            <ListItemIcon>
              <Gear />
            </ListItemIcon>
            <ListItemText primary="General Settings" />
          </StyledListItem>
        </AppLink>
        <AppLink to="/business/team-members" aria-label="Team Members">
          <StyledListItem
            onClick={() => setOpen(false)}
            selected={window.location.pathname.includes("team-members")}
          >
            <ListItemIcon>
              <Peoples />
            </ListItemIcon>
            <ListItemText primary="Team Members" />
          </StyledListItem>
        </AppLink>
        <StyledListItem
          onClick={() => handleClick("lead")}
          selected={window.location.pathname.includes("lead")}
        >
          <ListItemIcon>
            <Lead />
          </ListItemIcon>
          <ListItemText primary="Lead Management" />
          {open === "lead" || window.location.pathname.includes("lead") ? (
            <ExpandLess />
          ) : (
            <ExpandMore />
          )}
        </StyledListItem>
        <Collapse
          in={open === "lead" || window.location.pathname.includes("lead")}
          timeout="auto"
          className="submenu"
          unmountOnExit
        >
          <List component="div" disablePadding>
            <AppLink
              to="/business/lead-data-fields"
              aria-label="Lead Data Fields"
            >
              <StyledListItem
                sx={{ paddingLeft: "64px!important" }}
                selected={window.location.pathname.includes("lead-data-fields")}
                onClick={() => setOpen("lead")}
              >
                <ListItemText primary="Lead Data Fields" />
              </StyledListItem>
            </AppLink>
            <AppLink
              to="/business/lead-distribution-rules"
              aria-label="Lead Distribution Rules"
            >
              <StyledListItem
                sx={{ paddingLeft: "64px!important" }}
                selected={window.location.pathname.includes(
                  "lead-distribution-rules"
                )}
                onClick={() => setOpen("lead")}
              >
                <ListItemText primary="Lead Distribution Rules" />
              </StyledListItem>
            </AppLink>
          </List>
        </Collapse>
        <StyledListItem
          onClick={() => handleClick("whatsapp")}
          selected={window.location.pathname.includes("wa-")}
        >
          <ListItemIcon>
            <WhatsApp />
          </ListItemIcon>
          <ListItemText primary="WhatsApp" />
          {open === "whatsapp" || window.location.pathname.includes("wa-") ? (
            <ExpandLess />
          ) : (
            <ExpandMore />
          )}
        </StyledListItem>
        <Collapse
          in={open === "whatsapp" || window.location.pathname.includes("wa-")}
          timeout="auto"
          className="submenu"
          unmountOnExit
        >
          <List component="div" disablePadding>
            <AppLink
              to="/business/wa-basic-settings"
              aria-label="Basic Settings"
            >
              <StyledListItem
                sx={{ paddingLeft: "64px!important" }}
                selected={window.location.pathname.includes(
                  "wa-basic-settings"
                )}
                onClick={() => setOpen("whatsapp")}
              >
                <ListItemText primary="Basic Settings" />
              </StyledListItem>
            </AppLink>
            <AppLink
              to="/business/wa-usage-report"
              aria-label="Usage and Reports"
            >
              <StyledListItem
                sx={{ paddingLeft: "64px!important" }}
                selected={window.location.pathname.includes("wa-usage-report")}
                onClick={() => setOpen("whatsapp")}
              >
                <ListItemText
                  primary="Usage and Reports"
                  //
                />
              </StyledListItem>
            </AppLink>
            <AppLink
              to="/business/wa-template-messages"
              aria-label="Template Messages"
            >
              <StyledListItem
                sx={{ paddingLeft: "64px!important" }}
                selected={window.location.pathname.includes(
                  "wa-template-messages"
                )}
                onClick={() => setOpen("whatsapp")}
              >
                <ListItemText primary="Template Messages" />
              </StyledListItem>
            </AppLink>
          </List>
        </Collapse>
        <AppLink to="/business/social-media-linking">
          <StyledListItem
            onClick={() => setOpen(false)}
            selected={window.location.pathname.includes("social-media-linking")}
          >
            <ListItemIcon>
              <Social />
            </ListItemIcon>
            <ListItemText primary="Social Media Linking" />
          </StyledListItem>
        </AppLink>
      </List>
      <Box id="bottom">
        <Divider variant="fullWidth" sx={{ color: "#24272c1a" }} />
        <Box
          id="actions"
          sx={{
            display: "flex",
            py: 1.75,
            px: 1.5,
            justifyContent: "space-between",
          }}
        >
          <AppLink
            id="payment"
            to="/all-payments/subscriptions"
            sx={{
              color: "text.primary",
              cursor: "pointer",
              fontSize: 15,
              fontWeight: 500,
              lineHeight: "18px",
            }}
          >
            {"All Payments"}
          </AppLink>
          <Link
            underline="none"
            component="button"
            id="exit"
            onClick={() => exitSetting()}
            sx={{
              color: "#AD0000",
              cursor: "pointer",
              fontFamily: "Roboto",
              fontSize: 15,
              fontWeight: 500,
              lineHeight: "18px",
            }}
          >
            {"Exit Settings"}
          </Link>
        </Box>
      </Box>
    </Box>
  );
};

export default Sidebar;
