import { ReactElement, useState } from "react";
import { Tooltip } from "@mui/material";

import { TabToolTipSX } from "Containers/App/LayoutComponents";

export const ToolTippedTab = ({
  title,
  children,
}: {
  title: string;
  children: ReactElement;
}) => {
  const [toolTipState, setToolTipState] = useState<boolean>(false);
  return (
    <Tooltip
      title={title}
      placement="right"
      PopperProps={{
        sx: TabToolTipSX,
      }}
      open={toolTipState}
      onClose={() => setToolTipState(false)}
      onOpen={() => setToolTipState(true)}
      arrow
    >
      {children}
    </Tooltip>
  );
};
export default ToolTippedTab;
