import { Box, ListItemAvatar, Skeleton } from "@mui/material";
import { useState } from "react";

type ListItemImageProps = {
  alt: string;
  src: string;
  height?: number;
  width?: number;
};

export const ListItemImage = ({
  src,
  alt,
  height = 35,
  width = 35,
}: ListItemImageProps) => {
  const [loading, setLoading] = useState(true);
  const onImageLoad = () => {
    setLoading(false);
  };

  return (
    <ListItemAvatar
      sx={{
        alignItems: "center",
        bgcolor: "#ffffff",
        border: 1,
        borderColor: "#24272CB2",
        borderRadius: 1,
        display: "flex",
        justifyContent: "center",
        height,
        minWidth: "unset",
        p: 0.25,
        mr: 1.5,
        width,
      }}
    >
      {loading && (
        <Skeleton variant="rectangular" height={height} width={width} />
      )}
      {src && (
        <Box
          component="img"
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          height={loading ? 0 : "auto"}
          width={loading ? 0 : "100%"}
        />
      )}
    </ListItemAvatar>
  );
};
export default ListItemImage;
